@import url('https://fonts.googleapis.com/css2?family=Poppins&family=Ubuntu:wght@700&display=swap');

body,
html {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    background: #283149;
    height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Heading {
  font-size: 3rem;
  margin: 20px 0px;
  text-align: center;
  color: #DBEDF3;
  text-shadow: 3px 0 #da0463;
  font-family: 'Ubuntu', sans-serif;
}

.color-box {
  width: 340px;
  height: 150px;
  border: 2px solid #fff;
}

.guess-btn-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.guess-btn {
  padding: 10px 20px;
  margin-top: 10px;
  font-size: 16px;
  border: 1px solid #fff;
  border-radius: 2px;
}

.guess-btn:hover {
  background-color: black;
  color: #fff;
}

button + button {
  margin-left: 10px;
}

.answer {
  font-size: 1.5rem;
  color: #fff;
  margin: 20px;
  padding: 5px 10px;
  border-radius: 5px;
}

.correct-answer {
  background-color: green;
}

.wrong-answer {
  background-color: red;
}

@media (max-width: 786px) {

  .Heading {
    font-size: 2.6rem;
  }

  .color-box {
    width: 200px;
    height: 100px;
  }
  
  .guess-btn-section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  button + button {
    margin-left: 0px;
  }

  .guess-btn {
    margin-top: 15px;
  }
}